.realtime {
  width: 411px;
  height: 331px;
}
.realtime .title {
  width: 137px;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
  text-decoration: none;
  text-shadow: 0 0 30px #f4faf5;
}
.realtime .kuang {
  margin-top: 10px;
  width: 411px;
  height: 284px;
  overflow: hidden;
}
.realtime .kuang .bj {
  margin: 20px 0 0 13px;
  width: 397px;
  height: 246px;
  background-image: linear-gradient(90deg, rgba(2, 208, 255, 0.1) 0%, rgba(22, 32, 99, 0.1) 100%);
  overflow: hidden;
}
.realtime .kuang .bj ul {
  width: 100%;
  height: 52%;
  margin: 0;
  padding: 0;
}
.realtime .kuang .bj ul li {
  display: inline-block;
  background-position: 0 center;
  list-style: none;
  width: 112px;
  height: 125px;
  text-align: center;
  overflow: hidden;
  margin: 8px 0 0 15px;
}
.realtime .kuang .bj ul li p {
  margin-bottom: 0;
  font-family: 'webfont';
}
.realtime .kuang .bj ul li p i {
  font-style: normal;
  font-size: 12px;
  color: #ffffff;
}
.realtime .kuang .bj ul li p:nth-child(1) {
  margin-top: 20px;
  font-size: 12px;
  color: #ffffff;
}
.realtime .kuang .bj ul li p:nth-child(2) {
  margin-bottom: 5px;
  font-size: 20px;
  color: #00ffff;
  font-family: 'webfont';
}
.realtime .kuang .bj ul li p:nth-child(3) {
  width: 112px;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  border: 1px solid #0090ff;
  color: #ffffff;
  border-radius: 4px;
}
.realtime .kuang .bj ul li p:nth-child(4) {
  margin-top: 11px;
  font-size: 12px;
  color: #ffffff;
}
.realtime .kuang .bj .zhe {
  width: 100%;
  height: 100%;
}
.ws {
  width: 100%;
  height: 100%;
  display: inline-block;
  margin-left: 12px;
}
